@import 'src/style/variable.scss';

.goodsrow {
  height: 100%;
  width: 100%;
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  &__container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template: 1fr / 25% 1fr;

    &.like {
      width: 80%;
      grid-template: 1fr / 1fr;
      padding-bottom: 100px;
    }
  }

  @media (max-width: $mw1 + px) {
    &__container {
      width: 100%;
      grid-template: 1fr / 25% 1fr;

      &.like {
        width: 80%;
        grid-template: 1fr / 1fr;
      }
    }
  }
  @media (max-width: $mw2 + px) {
    &__container {
      width: 100%;
    }
    &__container {
      grid-template: 1fr / 1fr;

      &.like {
        width: 100%;
      }
    }
  }

  @media (max-width: $mw3 + px) {
  }
  @media (max-width: 700px) {
    &__container {
    }
  }
  @media (max-width: $mw4 + px) {
    &__container {
      justify-content: center;
    }
  }
  @media (max-width: $mw5 + px) {
    &__container {
      width: 100%;
    }
  }
  @media (max-width: 299px) {
    &__container {
      width: 80%;
      &.like {
        width: 80%;
      }
    }
  }
}
