@import 'src/style/variable.scss';

.likepagination {
  width: 100%;
  height: 130px;

  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  &__btn {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 15px;
    border: 1px solid var(--itemtext);
    border-radius: 5px;
    p {
      color: var(--itemtext);
      pointer-events: none;
    }
  }

  @media (max-width: $mw3 + px) {
    & {
      height: 110px;
    }
  }
  @media (max-width: $mw4 + px) {
  }
  @media (max-width: $mw4 + px) {
  }
}
