@import 'src/style/variable.scss';

.liketuner {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  p {
    color: var(--itemtext);
  }
  &__blocks {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  &__block {
    display: flex;
    position: relative;
    align-items: center;
  }

  &__text {
    padding-right: 10px;
  }

  &__number {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0px;
    width: 90px;
    border-bottom: 1px solid var(--itemtext);
    cursor: pointer;
    p {
      pointer-events: none !important;
    }
    p:first-child {
      padding-right: 5px;
      letter-spacing: 1px;
    }
  }

  &__arrow {
    transform: rotate(0deg);
    transition: transform 0.2s ease 0s;
  }

  &__limit.add .liketuner__arrow {
    transform: rotate(90deg);
  }

  ul {
    position: absolute;
    width: 100%;
    top: 110%;
    max-height: 0;
    z-index: 2;
    background: var(--gray);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    transition: all 0.2s linear 0s;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    border-radius: 2px;
    &.add {
      opacity: 1;
      visibility: visible;
      max-height: 120px;
    }
  }

  li {
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    color: var(--itemtext);
    background: var(--gray);
    cursor: pointer;
    outline: none;
    border: none;
    width: 100%;
    p {
      color: var(--itemtext);
      line-height: 130%;
      pointer-events: none !important;
    }
    &:hover {
      background: var(--oldRed);
      p {
        color: #fff !important;
      }
    }
    &.add {
      background: var(--oldRed);
      p {
        color: #fff !important;
      }
    }
  }

  @media (max-width: $mw3 + px) {
    & {
      height: 110px;
    }
  }
  @media (max-width: $mw4 + px) {
  }
  @media (max-width: $mw4 + px) {
    & {
      height: 80px;
    }
  }
}
