@import 'src/style/variable.scss';

.pagination {
  width: 100%;
  height: 130px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__back,
  li {
    padding-right: 15px;
    cursor: pointer;
    color: var(--itemtext);
    &:hover {
      color: var(--oldRed);
    }
    &.add {
      color: var(--oldRed);
    }
  }
  &__back:last-child {
    padding-right: 0;
  }
  @media (max-width: $mw3 + px) {
    & {
      height: 110px;
    }
  }
}
