@import '../../../style/variable.scss';

.likesGoods {
  // Goods ====================================
  &__table {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background: var(--gray);
    padding-top: 10px;
    padding-bottom: 30px;
    min-height: 100vh;
  }

  &__goods {
    display: grid;
    width: 100%;
    justify-content: center;
    grid-gap: 20px;
    grid-template-columns: repeat(4, minmax(264px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(100px, 350px));
  }

  &__item {
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    color: #000;
    align-items: center;
    transition: all 0.5s ease 0s;
    justify-content: space-between;
    width: 100%;
    height: 350px;
    background-image: var(--goodsBack);
  }
  &__body {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    flex: 1;
    padding: 0 15px;
  }

  &__img {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    margin-top: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 100%;
    height: 80%;
    transition: transform 1s ease 0s;
    overflow: hidden;
  }
  &__select {
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #b03d26;
    width: 100%;
    cursor: pointer;
    opacity: 0.8;
    color: var(--oldgray);
    font-size: calc(16px + (22 - 16) * ((100vw - 320px) / (1400 - 320)));
    color: #fff;
    text-transform: uppercase;
  }
  &__select:hover {
    opacity: 1;
  }

  &__heart {
    padding-right: 0px;
    opacity: 0;
    cursor: pointer;
    width: 25px;
    height: 25px;
    transform: scale(1);
    svg {
      width: 100%;
      height: 100%;
      fill: var(--oldgray);
    }
    &.add svg {
      fill: var(--oldRed) !important;
    }
  }

  @keyframes knockHeart {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0.9);
    }
  }

  &__item:hover &__heart {
    opacity: 0.8;
  }
  &__heart:hover {
    opacity: 1 !important;
    animation: knockHeart 0.5s ease 0s infinite;
  }

  &__item:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
  }

  &__text {
    padding-top: 5px;
    display: flex;
    flex-direction: column;
  }

  &__text_name {
    display: flex;
    color: var(--oldgray);
  }
  &__text_name div:first-child {
    padding-right: 10px;
  }
  &__text_category {
    color: rgb(121, 121, 121);
    margin-bottom: 5px;
  }

  &__item:hover &__addBasket {
    opacity: 1;
  }

  &__filter {
    display: none;
  }
  @media (max-width: $mw2 + px) {
    &__goods {
      grid-gap: 15px;
      grid-template-columns: repeat(3, minmax(100px, 1fr));
    }
  }
  @media (max-width: $mw3 + px) {
    &__goods {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
      grid-template-rows: repeat(auto-fit, minmax(100px, 1fr));
    }
    &__item {
      width: 100%;
      height: 100% !important;
    }
    &__text {
      padding-top: 5px;
    }
    &__text_name {
      margin-bottom: 0px;
    }
    &__img {
      width: 100%;
      height: 200px;
    }

    &__category {
      flex-direction: row;
      width: 100%;
      align-items: center;
      text-align: center;
    }
    &__category div {
      padding: 5px;
    }
    &__heart,
    &__addBasket {
      opacity: 1;
    }
    &__item:hover {
      box-shadow: none;
      transform: scale(1);
    }
  }
  @media (max-width: $mw4 + px) {
    &__img {
      width: 100%;
      height: 180px;
    }
    &__text {
      width: 100%;
    }
    &__text_name {
      justify-content: flex-start;
      width: 100%;
    }
    &__select {
      height: 35px;
      font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1400 - 320)));
    }
  }
  @media (max-width: 320px) {
    &__goods {
      grid-template-columns: repeat(1, minmax(100px, 1fr));
      grid-template-rows: repeat(auto-fit, minmax(100px, 1fr));
    }
  }
}
