@import 'src/style/variable.scss';

.product {
  &__table {
    height: 100%;
  }

  &__goods {
    display: grid;
    width: 100%;
    justify-items: center;
    grid-gap: 20px;
    grid-template-columns: repeat(4, minmax(240px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(100px, 1fr));
  }

  &__item {
    display: grid;
    grid-template: 1fr 90px 40px 40px / 100%;
    border-radius: 5px;
    transition: all 0.5s ease 0s;
    overflow: hidden;
    width: 100%;
    height: 360px;
    background: var(--goodsBack);
  }

  &__textbottom {
    display: flex;
    flex-wrap: wrap;
  }

  &__blockimage {
    width: 100%;
    height: 100%;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    position: relative;
  }

  &__heart {
    position: absolute;
    z-index: 1;
  }
  &__sale {
    position: absolute;
    left: 10px;
    top: 10px;
    z-index: 1;
    p {
      color: #fff;
    }
  }

  &__saleback {
    z-index: 1;
    position: relative;
    width: 42px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__saletext {
    z-index: 1;
    position: absolute;
  }
  &__saleground {
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #b03d26;
    transform: skewX(-10deg);
  }

  &__img {
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: transform 1s ease 0s;
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: 0 15px;
  }

  &__viewmore {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #b03d26;
    width: 100%;
    cursor: pointer;
    opacity: 0.8;
    color: var(--oldgray);
    font-size: calc(16px + (22 - 16) * ((100vw - 320px) / (1400 - 320)));
    color: #fff;
    text-transform: uppercase;
  }
  &__viewmore:hover {
    opacity: 1;
  }

  &__item:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
  }

  &__text {
    align-self: start;
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    p {
      color: var(--oldgray);
      line-height: 20px;
    }
  }

  &__price {
    display: flex;
    align-items: center;
    padding-bottom: 5px;
    p:first-child {
      line-height: 130%;
      font-size: 20px;
    }
    &.add {
      p:first-child {
        padding-right: 10px;
      }
      p:last-child {
        text-decoration: line-through;
        color: rgb(83, 83, 83);
        font-size: 1em;
      }
    }
  }

  &__name {
    display: flex;
    justify-content: space-between;
    color: var(--oldgray);
  }
  &__category {
    color: rgb(83, 83, 83);
  }

  &__item:hover &__addBasket,
  &__item:hover &__sale {
    opacity: 1;
  }

  &__filter {
    display: none;
  }

  @media (max-width: $mw1 + px) {
    &__goods {
      grid-gap: 15px;
      grid-template-columns: repeat(3, minmax(100px, 1fr));
      justify-items: end;
    }
  }
  @media (max-width: $mw3 + px) {
    &__addBasket {
      opacity: 1;
    }
    &__item:hover {
      box-shadow: none;
      transform: scale(1);
    }
  }

  @media (max-width: $mw4 + px) {
    &__text {
      align-self: flex-start;
      width: 100%;
    }

    &__text_name {
      justify-content: flex-start;
      width: 100%;
    }
  }
  @media (max-width: 700px) {
    &__goods {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
    }
    &__name {
      display: flex;
      flex-direction: row;
      p {
        line-height: 18px;
      }
    }
    &__item:hover &__heart {
      opacity: 1;
    }

    &__text {
      width: 100%;
      padding: 5px 5px 5px 5px;
    }
    &__text_name {
      justify-content: flex-start;
      width: 100%;
    }

    &__item {
      grid-template: 180px 90px 40px 40px / 100%;
      height: auto;
    }
    &__price {
      padding: 5px 0 5px 0;
    }
  }
  @media (max-width: $mw5 + px) {
    &__goods {
      grid-template-columns: repeat(2, minmax(100px, 1fr));
    }
    &__blockimage {
      padding: 5px;
      padding-bottom: 0;
    }
    &__name {
      display: flex;
      flex-direction: column;
      p {
        line-height: 16px;
      }
    }
    &__item:hover &__heart {
      opacity: 1;
    }
    &__item {
      grid-template: 140px 75px 32px 32px / 100%;
      height: auto;
    }
    &__text {
      width: 100%;
      padding: 5px 5px 5px;
      h3:first-child {
        padding-right: 5px;
      }
    }

    &__price {
      p:first-child {
        font-size: 18px;
        line-height: 20px;
      }
    }

    &__textbottom {
      p {
        font-size: 12px;
      }
    }
    &__text_name {
      justify-content: flex-start;
      width: 100%;
    }
    &__saleback {
      width: 35px;
      height: 25px;
    }
  }
  @media (max-width: 320px) {
    &__item {
      grid-template: 120px 70px 28px 28px / 100%;
    }

    &__name {
      p {
        font-size: 10px !important;
        line-height: 14px;
      }
    }
    &__img {
      margin: 0 5px;
    }

    &__viewmore,
    &__addBasket {
      p {
        font-size: 12px !important;
      }
    }
    &__saleback {
      width: 30px;
      height: 20px;
      p {
        font-size: 10px !important;
      }
    }

    &__price {
      padding-bottom: 2px;
      p:first-child {
        font-size: 16px;
      }
      &.add {
        p:first-child {
          padding-right: 8px;
        }
      }
    }
  }

  @media (max-width: 299px) {
    &__name {
      flex-direction: row;
      h4 {
        padding-bottom: 0px;
      }
    }
    &__img {
      margin: 0 15px;
    }
    &__goods {
      grid-template-columns: repeat(1, minmax(100px, 1fr));
      grid-template-rows: repeat(auto-fit, minmax(100px, 1fr));
    }
    &__item {
      grid-template: 140px 70px 32px 32px / 100%;
    }
    &__text {
      justify-self: center;
      align-self: center;
      width: 80%;
      padding: 0 0 0 0;
    }
  }
}

.rating {
  position: absolute;
  right: 10px;
  bottom: 0px;
  z-index: 1;
  user-select: none;
  &__body {
    position: relative;
    width: 30px;
    height: 30px;

    svg {
      width: 100%;
      height: 100%;
      fill: var(--oldyellow);
      stroke: var(--oldgray);
      stroke-width: 2;
    }
  }

  &__star {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__text {
    width: 100%;
    height: 100%;
    top: 1.5px;
    position: absolute;
    color: var(--oldgray);
    opacity: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      font-size: 11px !important;
    }
  }
  @media (max-width: $mw3 + px) {
    & {
      right: auto;
    }
  }
  @media (max-width: $mw4 + px) {
    &__body {
      width: 25px;
      height: 25px;
    }
  }
  @media (max-width: $mw5 + px) {
    &__body {
      width: 22px;
      height: 22px;
    }
  }
  @media (max-width: 310px) {
    &__body {
      position: relative;
      width: 20px;
      height: 20px;
      p {
        font-size: 10px !important;
      }
    }
  }
}
